
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mavonEditor } from 'mavon-editor';
import 'mavon-editor/dist/css/index.css'
export default{
name:"create",
components:{
Group,
XTextarea,
XButton ,
mavonEditor
},
data() {
return {
title:"", //发表主题的标题
content:"", //发表主题的内容
toolbars: {
bold: true, // 粗体
italic: true, // 斜体
header: true, // 标题
underline: true, // 下划线
strikethrough: true, // 中划线
mark: true, // 标记
superscript: true, // 上角标
subscript: true, // 下角标
quote: true, // 引用
ol: true, // 有序列表
ul: true, // 无序列表
link: true, // 链接
imagelink: true, // 图片链接
code: true, // code
table: true, // 表格
htmlcode: true, // 展示html源码
help: true, // 帮助
/* 2.1.8 */
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true, // 右对齐
/* 2.2.1 */
subfield: true, // 单双栏模式
preview: true, // 预览
}
}
},
methods: {
handleTopic(){
var app=this;
this.$http.post("/topics",{
accesstoken:this.$store.state.accesstoken,
tab:"dev",
title:this.title,
content:this.content
}).then(function(res){
console.log(res.data);
if(res.data.success){

app.$router.push("/topiclist/dev")
}
})
}
},
}



