
import Msg from 'vux/src/components/msg/index.vue'

import { mapMutations } from 'vuex';
export default {
name:"logout",
data() {
return {
buttons:[
{
text:"跳转至登录",
type:"primary",
link:"/login"
}
]
}
},
components:{
Msg
},

methods: {
...mapMutations(['mutationsMsgCount'])
},
created() {
if(this.$store.state.isLogined==false){

return;
}

window.localStorage.clear();

this.$store.commit("mutationsClear");


this.$router.push("/topiclist/all");

this.mutationsMsgCount(0)
},
}



