import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home.vue'
import TopicList from '../components/TopicList.vue'
import Topic from '../components/Topic.vue'
import Center from '../components/Center.vue'
import Msgs from '../components/Msgs.vue'
import Logout from '../components/Logout.vue'
import Login from '../components/Login.vue'
import User from '../components/User.vue'
import Create from '../components/Create.vue'
import Edit from '../components/Edit.vue'


Vue.use(VueRouter);

const routes=[
  // {
  //   path:"/",
  //   component:Home
  // },
  { //默认加载页面 显示全部分类的主题列表
    path:"/",
    redirect:"/topiclist/all"
  },
  //主题列表(对应分类加载)
  {
    path:"/topiclist/:name",
    name:"topiclist",
    component:TopicList,
    props:true
  },
  //主题详情(id值)
  {
    path:"/topic/:id",
    name:"topic",
    component:Topic,
    props:true
  },
  {//登录
    path:"/login",
    name:"login",
    component:Login
  },
  {//个人中心
    path:"/center",
    name:"center",
    component:Center
  },
  {//消息中心
    path:"/msgs",
    name:"msgs",
    component:Msgs
  },
  {//退出
    path:"/logout",
    name:"logout",
    component:Logout
  },
  {//用户中心
    path:"/user/:loginname",
     name:"user",
    component:User,
    props:true
  },
  {//发表话题
    path:"/create",
    name:"create",
    component:Create
  },
  {//编辑话题
    path:"/topic/:id/edit",
    name:"edit",
    component:Edit,
    props:true
  },
];

const router=new VueRouter({
  routes
})
export default router;


