
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import {mapState,mapGetters,mapMutations, mapActions} from 'vuex';
import VmBackTop from "vue-multiple-back-top";
export default {
name: 'app',
data() {
return {

}
},
computed:{
...mapState(['msgCount']),
...mapGetters(['moreMenus']),

isShow:{

get(){
return this.$store.state.isShow;
},
set(nval){
return this.$store.commit('mutationsIsShow',nval)
}
}
},
components:{
XHeader,
Actionsheet,
VmBackTop,
Badge
},
methods: {
showMoreMenus(){
this.isShow=true;
},

handleActionsheet(menuKey){
this.$router.push("/"+menuKey)
},

handleTitle(){
this.$router.push("/")
},
...mapMutations(['mutationsLogined','mutationsLoginname','mutationsAccessToken','mutationsAuthorId']),
...mapActions(['getMsgsCount'])


},

created() {

var loginStatus=window.localStorage.getItem("loginStatus");

if(loginStatus){
this.mutationsLogined(); 
this.mutationsLoginname(window.localStorage.getItem("loginname"));
this.mutationsAccessToken(window.localStorage.getItem("accesstoken"));
this.mutationsAuthorId(window.localStorage.getItem("author_id"));


this.getMsgsCount();
var app=this;
window.setInterval(function(){
app.getMsgsCount();
},1000*60*5);

}


},


}



