import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations';
import actions from './actions';

Vue.use(Vuex);

const store =new Vuex.Store({
    state:{
        topiclists:[
            {name:"all","desc":"全部"},
            {name:"good","desc":"精华"},
            {name:"share","desc":"分享"},
            {name:"ask","desc":"问答"},
            {name:"job","desc":"招聘"},
            {name:"dev","desc":"测试"}
          ],
          isShow:false,//显示更多菜单
          isLogined:false,//显示登录的状态
          //登录后的菜单
          loginedMenus:{
            create:"发表新话题",
            center:"个人中心",
            msgs:"消息中心",
            logout:"退出"
          },
          //未登录的菜单
          unloginedMenus:{
            login:"登录",
          },
          loginname:"",//用来保存登录后的用户名
          author_id:"",//保存用户登录的id
          accesstoken:"",//用来保存登录后的访问令牌
          msgCount:"",//未读数量
    },
    //状态数据的计算属性
    getters:{
      //根据登录状态 显示不同的更多菜单
      moreMenus(state){
        //判断用户是否登录
        if(state.isLogined){//登录状态
            return state.loginedMenus;//显示登录后的菜单
        }
        return state.unloginedMenus;//显示未登录的菜单
      },
      //处理分类显示汉字
      fileterTab(state){
        return function(tab){
          for(var i=0;i<state.topiclists.length;i++){
            const element=state.topiclists[i];
              if(element.name == tab){
                return state.topiclists[i].desc;
              }
            
        }
        }
      }
    },
    mutations,
    actions
});
export default store;


