import request from '../ajax'
const actions={
     //获取未读消息数量
     getMsgsCount(context){
          return request.get("/message/count?accesstoken="+context.state.accesstoken).then(function(res){
            console.log(res.data);
            if(res.data && res.data.success){
                context.commit("mutationsMsgCount",res.data.data);
            }
          })
      }

}
export default actions;


